.seperator {
    width: 100%;
}

.controls-wrapper {
    width: 100%;
}

.carousel-wrapper {
    position: relative;
    margin: 0 auto;
    width: 90%;
    height: 20%;
}

.carousel-wrapper .rec.rec-slider-container {
    margin: 0;
    padding: 0;
    list-style: none;
    position: relative;
    transition: transform 0.3s ease;
}

.carousel-wrapper .rec-arrow {
    background-color: transparent;
    color: #000;
    font-size: 1.5rem;
    border: none;
    box-shadow: none;
    margin-top: -20px;
}

@media only screen and (max-width: 768px) {
    .carousel-wrapper .rec-arrow {
      font-size: 1rem;
    }
  }

.carousel-wrapper .rec-arrow:hover {
    background-color: transparent;
    color: #000;
    box-shadow: none;
}

.carousel-wrapper .rec-arrow:disabled {
    opacity: 0.4;
    cursor: default;
}


.carousel-wrapper .rec.rec-pagination {
    display: none;
}

.carousel-wrapper .rec.rec-item-wrapper {
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    height: 100%;
}

.carousel-wrapper .rec.rec-item-wrapper img {
    max-width: 100%;
    max-height: 100%;
}

.item {
    background-color: #f9f9f9;
    border: 1px solid #dddddd;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: #333333;
    padding: 15px;
    text-align: center;
    height: 85%;
    margin-top: 20px;
    margin-bottom: 20px;
    margin-left: 5px;
    margin-right: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

@media only screen and (max-width: 600px) {
    .item {
        font-size: 14px;
    }
}

.item img {
    max-width: 100%;
    max-height: 100px;
    margin-bottom: 10px;
}

